<template>
  <div :class="{ 'focused': hasData, 'error': displayError }" class="input-group phone">
    <label @click="focusInput" v-html="buildLabel" />
    <vue-tel-input
      :value="localValue"
      @onInput="handleInput"
      @onBlur="unfocusInput"
    />
    <CompleteIcon :display="isComplete" />
    <InputInfoSection
      :has-hint="hasHint"
      :hint="hint"
      :display-error="displayError"
      :error-value="errorValue"
    />
  </div>
</template>

<script>
import { inputValidation } from '../../mixins/inputValidation'
import VueTelInput from 'vue-tel-input'
import 'vue-tel-input/dist/vue-tel-input.css'
import CompleteIcon from './fragments/CompleteIcon'
import InputInfoSection from './fragments/InputInfoSection'

export default {
  name: 'PhoneInput',
  mixins: [ inputValidation ],
  components: {
    VueTelInput,
    CompleteIcon,
    InputInfoSection
  },
  data () {
    return {
      localValue: '',
      tmpValue: '',
      completedNumber: false,
      validPhone: false
    }
  },
  created () {
    if (this.$store.getters.getClientPhone) {
      this.localValue = this.$store.getters.getClientPhone
    }
    this.isComplete = this.localValue.length > 0 && !this.hasError
  },
  methods: {
    handleInput ({ number, isValid }) {
      this.tmpValue = number
      this.completedNumber = isValid
      if (isValid) {
        this.localValue = number
        this.validPhone = this.completedNumber = isValid
        if (this.validate()) {
          this.emitInput(number)
        }
      } else{
        this.localValue = ''
        this.emitInput(false)
      }
    },
    focusInput () {
      this.isFocused = true
      this.$el.children[1].children[1].focus()
    },
    unfocusInput () {
      if (!this.completedNumber) { this.validate() }
    }
  }
}
</script>
