import { inputStyler } from './inputStyler'
const emailValidator = require('email-validator')

const randId = () => {
  return Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1)
}

const inputValidation = {
  mixins: [ inputStyler ],
  props: {
    name: {
      type: String,
      default: ''
    },
    inputId: {
      type: String,
      default: randId()
    },
    inputType: {
      type: String,
      default: 'text'
    },
    isRequired: {
      type: Boolean,
      default: true
    },
    requiresPrevInput:{
      type: Boolean,
      default: false
    },
    isDisabled:{
      type: Boolean,
      default: false
    },
    validation: {
      type: String,
      default: ''
    },
    minValue: {
      type: Number,
      default: 1
    },
    maxValue: {
      type: Number,
      default: 1
    },
    inputPrice: {
      type: Number,
      default: 0
    }
  },
  computed: {
    hasRequiredData (){
      if (this.requiredData.hasOwnProperty('data')) {
        return !this.requiredData['data']
      }
    }
  },
  methods: {
    /*
    ** Single validation method that applies appropriate
    ** individual validations depending on selected validation type
    */
    validate () {
      this.valid = true
      this.validateRequired()

      if (this.valid){
        /** If none is provided, validates as text */
        switch (this.validation){
          case 'email':
            this.validateEmail()
            break
          case 'number':
            this.validateNumber()
            break
          case 'phone':
            this.validatePhone()
            break
          case 'date':
            this.valid = this.selectedDate
            break
        }
      }
      /** Global cleanup of errors after running all Switch cases */
      if (this.valid) {
        this.clearError()
        this.setComplete(this.hasData)
      }
      return this.valid
    },
    /** Simply makes sure a value exists for the input type, regardles of what it's used for */
    validateRequired () {
      if (this.isRequired) {
        switch (this.inputType) {
          case 'text':
          case 'email':
            this.valid = !(this.localValue.trim().length === 0)
            break
          case 'phone':
            /** Phone has own required validation as input may exist but be invalid phone format */
            this.validatePhone()
            break
          case 'number':
            this.valid = typeof this.localValue === 'number' && !isNaN(this.localValue)
            break
        }
      }
      if (!this.valid && this.inputType !== 'phone') { this.setError('Required input') }
    },
    isValidEmail () { return emailValidator.validate(this.localValue) },
    validateEmail () {
      if (!this.isValidEmail()) {
        this.valid = false
        this.setError('Enter a valid email')
      }
    },
    validatePhone () {
      if (this.tmpValue.trim().length > 0) {
        if (!this.validPhone) {
          this.valid = false
          this.setError('Enter a valid phone number')
        }
      } else{
        this.valid = false
        this.setError('Required input')
      }
    },
    validateNumber () {
      if (this.localValue < this.minValue){
        this.valid = false
        this.setError(`Minimum pax is ${ this.minValue }`) 
      }
      if (this.localValue > this.maxValue){
        this.valid = false
        this.setError(`Maximum pax is ${ this.maxValue }`) 
      }
    },
    emitInput (value) {
      this.$emit('input', value)
    }
  }
}

export { inputValidation }
