<template>
  <section class="inner-section">
    <h2 class="section-title">Select day and time</h2>
    <div class="form-section">
      <DateInput
        :label="'Date'"
        :name="'date'"
        :validation="'date'"
        v-model="localDate"
      />
    </div>
    <div class="form-section">
      <TimeInput
        :label="'Time'"
        :name="'time'"
        :is-disabled="!selectedDate"
        :is-loading="isLoading"
        :timeslots="availableTimeslots"
        :requires-prev-input="true"
        v-model="localTime"
      />
    </div>
    <div v-if="selectedTime" class="pax">
      <h2 class="section-title">Select passengers</h2>
      <Prices :prices="availablePrices" />
    </div>
    <div class="form-section">
      <DesktopCTA :can-proceed="canProceed"/>
    </div>
  </section>
</template>

<script>
import DateInput from '../../components/inputs/DateInput.vue'
import TimeInput from '../../components/inputs/TimeInput.vue'
import Prices from './Prices.vue'
import DesktopCTA from './DesktopCTA.vue'
import moment from 'moment'

export default {
  name: 'Booking',
  components: {
    DateInput,
    Prices,
    TimeInput,
    DesktopCTA
  },
  data () {
    return {
      localDate: this.$store.getters.getDate,
      localTime: this.$store.getters.getTime,
      isLoading: false
    }
  },
  computed: {
    availableTimeslots () { return this.$store.getters.getSlots },
    availablePrices () { return this.$store.getters.getPrices },
    selectedDate () {
      let hasDate = this.localDate || this.$store.getters.getDate
      if (hasDate) {
        this.$store.dispatch('saveSelectedDate', hasDate)
        this.getTimeSlots()
      }
      return hasDate
    },
    selectedTime () {
      let newTime = this.localTime
      let currentTime = this.$store.getters.getTime

      if (currentTime) {
        if (currentTime.id !== newTime.id){
          this.$store.dispatch('saveSelectedTime', newTime)
          this.loadPrices(newTime)
        }
      } else if(newTime) {
        this.$store.dispatch('saveSelectedTime', newTime)
        this.loadPrices(newTime)
      }
      return this.localTime || this.$store.getters.getTime
    },
    maxCapacity () { return this.$store.getters.getMaxCapacity },
    price () { return this.$store.getters.getPrice },
    canProceed () {
      let proceed = false
      if (this.localDate && this.localTime && Object.keys(this.availablePrices).length > 0) {
        if (this.hasPassengers()) {
          this.$store.dispatch('saveSelectedDate', this.localDate)
          this.$store.dispatch('saveSelectedTime', this.localTime)
          proceed = true
        }

      }
      this.$store.dispatch('changeFormProceed', proceed)
      return proceed
    }
  },
  created () {
    this.$store.dispatch('changeFormProceed', false)
    this.$store.dispatch('changeCurrentStep', 'stepOne')
  },
  methods: {
    getTimeSlots () {
      let date = moment(this.$store.getters.getDate).format('YYYY-MM-DD')
      this.isLoading = true

      this.$store.dispatch('getTimeSlots', {
        id: this.$store.getters.getActivityId,
        start: date,
        end: date
      }).then(res => {
        this.$store.dispatch('saveSlots', res.data.data)
        this.isLoading = false
      })
    },
    loadPrices (timeSlot){
      this.$store.dispatch('getPrices', timeSlot.prices).then(res =>{
        if (res) { this.$store.dispatch('savePrices', res.data.data) }
      })
    },
    hasPassengers (){
      let totalPax = 0
      let prices = this.availablePrices
      for (var key in prices) {
        totalPax += prices[key].selectedPax
      }
      return totalPax > 0
    }
  }
}
</script>
