<template>
<div class="custom-control custom-toggle">
  <input :id="toggleId" :checked="isToggled" :name="toggleId" type="checkbox" class="custom-control-input" @click="toggleChoice">
  <label class="custom-control-label" :for="toggleId">{{ label }}</label>
</div>
</template>

<script>
export default {
  name: 'ToggleInput',
  props: {
    toggleId: {
      type: String,
      default: 'shuttleToggle'
    },
    label: {
      type: String,
      default: 'Need a shuttle?'
    },
    isToggled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    toggleChoice () {
      this.$store.dispatch('saveShuttle', !this.isToggled)
    }
  }
}
</script>

