<template>
  <div :class="{ 'focused': hasData, 'error': displayError }" class="input-group">
    <label @click="focusInput" v-html="buildLabel" />
    <input
      ref="inputElement"
      tabindex="1"
      :value="localValue"
      :name="name"
      :type="inputType"
      @blur="unfocusInput($event)"
      @input="handleInput($event)"
    >
    <CompleteIcon :display="isComplete" />
    <InputInfoSection
      :has-hint="hasHint"
      :hint="hint"
      :display-error="displayError"
      :error-value="errorValue"
    />
  </div>
</template>

<script>
import { inputValidation } from '../../mixins/inputValidation'
import CompleteIcon from './fragments/CompleteIcon'
import InputInfoSection from './fragments/InputInfoSection'

export default{
  name: 'TextInput',
  mixins: [ inputValidation ],
  components: {
    CompleteIcon,
    InputInfoSection
  },
  data () {
    return {
      localValue: ''
    }
  },
  mounted () {
    switch (this.name) {
      case 'name':
        if (this.$store.getters.getClientName) {
          this.localValue = this.$store.getters.getClientName
        }
        break
      case 'address':
        if (this.$store.getters.getAddress) {
          this.localValue = this.$store.getters.getAddress
        }
        break
      case 'room':
        if (this.$store.getters.getRoom) {
          this.localValue = this.$store.getters.getRoom
        }
        break
    }
    this.isComplete = this.localValue.length > 0 && !this.hasError
  },
  methods: {
    focusInput () {
      this.isFocused = true
      this.$nextTick(() => this.$refs.inputElement.focus())
    },
    handleInput (e) {
      this.localValue = e.target.value
      this.$nextTick(() => this.$refs.inputElement.focus())
      this.clearError()
      this.emitInput(e.target.value)
    },
    unfocusInput (e) { this.emitInput(e.target.value.trim()) },
    emitInput (value) {
      if (this.validate()) {
        this.$emit('input', value)
      } else {
        this.$emit('input', false)
      }
    }
  }
}
</script>
