<template>
  <section class="inner-section px-15">
    <h2 class="section-title">Payment Details</h2>
    <div class="form-section">
      <div class="form-section">
        <RadioInputGroup
          :alternative-class="'horizontal payment'"
          :choices="choices"
        />
      </div>
    </div>
    <div class="form-section">
      <CardForm
        v-if="selectedMethod === 'card'"
        :proceed-payment="proceedPayment"
      />
      <PayPalForm
        v-if="selectedMethod === 'paypal'"
        :proceed-payment="proceedPayment"
      />
      <IdealForm
        v-if="selectedMethod === 'ideal'"
        :proceed-payment="proceedPayment"
      />
    </div>
    <div class="form-section">
      <DesktopCTA
        :can-proceed="canProceed"
        :custom-label="paymentLabel"
        @click="performPayment"
      />
    </div>
  </section>
</template>

<script>
import RadioInputGroup from '../../components/inputs/RadioInputGroup.vue'
import DesktopCTA from './DesktopCTA.vue'
import CardForm from './fragments/CardForm.vue'
import PayPalForm from './fragments/PayPalForm.vue'
import IdealForm from './fragments/IdealForm.vue'

export default {
  name: 'Payment',
  components: {
    RadioInputGroup,
    CardForm,
    PayPalForm,
    IdealForm,
    DesktopCTA
  },
  data (){
    return {
      proceedPayment: false,
      paymentLabel: 'Pay now',
      choices: [
        {
          label: 'Card',
          value: 'card',
          name: 'paymentMethod'
        },
        {
          label: 'PayPal',
          value: 'paypal',
          name: 'paymentMethod'
        },
        {
          label: 'iDeal',
          value: 'ideal',
          name: 'paymentMethod'
        }
      ]
    }
  },
  computed: {
    canProceed () { return this.$store.getters.canProceed },
    selectedMethod (){ return this.$store.getters.paymentOption }
  },
  watch:{
    // Change button label depending on selected method
    selectedMethod(){
      if (this.selectedMethod === 'card' || this.selectedMethod === 'paypal'){
        this.paymentLabel = 'Pay now'
      } else if (this.selectedMethod === 'ideal'){
        this.paymentLabel = 'Authorize payment'
      }
      this.$store.dispatch('changeFormProceed', false)
    }
  },
  created () {
    if (!this.$store.getters.getCurrentStep) { this.$router.push('/') }
    this.$store.dispatch('changeFormProceed', false)
    this.$store.dispatch('changeCurrentStep', 'stepFour')
  },
  methods:{
    // Triggered once clicking destop CTA (todo, same thing for mobile CTA)
    performPayment () {
      this.proceedPayment = true
      setTimeout(()=>{
        this.proceedPayment = false
      }, 1000)
    }
  }
}
</script>
