<template>
  <section class="inner-section">
    <h2 class="section-title">Select Extras</h2>
    <div class="form-section">
      <ToggleInput :is-toggled="needsShuttle" />
    </div>
    <div v-if="needsShuttle">
      <div class="form-section">
        <NumberInput
          :label="'Number of people'"
          :name="'shuttle-pax'"
          :validation="'number'"
          :is-disabled="!needsShuttle"
          :requires-prev-input="true"
          :min-value="1"
          :max-value="maxCapacity"
          v-model="shuttlePax"
        />
      </div>
      <div class="form-section">
        <TextInput
          :label="'Pickup address'"
          :name="'address'"
          :hint="'Hotel name, street address'"
          :is-disabled="!needsShuttle"
          :requires-prev-input="true"
          v-model="selectedAddress"
        />
      </div>
      <div class="form-section">
        <TextInput
          :label="'Room number'"
          :name="'room'"
          :is-required="false"
          v-model="selectedRoom"
        />
      </div>
      <div class="form-section">
        <RadioInputGroup
          :name="'transferType'"
          :choices="choices"
        />
      </div>
    </div>
    <div class="form-section">
      <TextareaInput
        :label="'Additonal Notes'"
        :name="'notes'"
        :is-required="false"
        v-model="notes"
      />
    </div>
    <div class="form-section">
      <DesktopCTA :can-proceed="canProceed"/>
    </div>
  </section>
</template>

<script>
import ToggleInput from '../../components/inputs/ToggleInput.vue'
import NumberInput from '../../components/inputs/NumberInput.vue'
import TextInput from '../../components/inputs/TextInput.vue'
import RadioInputGroup from '../../components/inputs/RadioInputGroup.vue'
import TextareaInput from '../../components/inputs/TextareaInput.vue'
import DesktopCTA from './DesktopCTA.vue'

export default {
  name: 'Extras',
  components: {
    ToggleInput,
    NumberInput,
    TextInput,
    RadioInputGroup,
    TextareaInput,
    DesktopCTA
  },
  data () {
    return {
      shuttlePax: this.$store.getters.getShuttlePax,
      selectedAddress: this.$store.getters.getAddress,
      selectedRoom: this.$store.getters.getRoom,
      shuttleType: this.$store.getters.getShuttleType,
      notes: this.$store.getters.getNotes,
      choices: [
        {
          label: 'Roundtrip',
          value: 'roundtrip',
          name: 'transferType'
        },
        {
          label: 'One-way, to experience',
          value: 'oneway-to',
          name: 'transferType'
        },
        {
          label: 'One-way, from experience',
          value: 'oneway-from',
          name: 'transferType'
        }
      ]
    }
  },
  computed: {
    needsShuttle () { return this.$store.getters.getShuttle },
    maxCapacity () { return this.$store.getters.getMaxCapacity },
    
    canProceed () {
      let proceed = true
      if (this.needsShuttle) {
        if (this.shuttlePax && this.selectedAddress) {
          this.$store.dispatch('saveShuttlePax', this.shuttlePax)
          this.$store.dispatch('saveAddress', this.selectedAddress)
          this.$store.dispatch('saveRoom', this.selectedRoom)
          this.$store.dispatch('saveShuttleType', this.shuttleType)

        } else { proceed = false }
        
      }
      this.$store.dispatch('saveNotes', this.notes)
      this.$store.dispatch('changeFormProceed', proceed)
      return proceed
    }
  },
  created () {
    // if (!this.$store.getters.getCurrentStep) { this.$router.push('/') }
    this.$store.dispatch('changeCurrentStep', 'stepThree')
  }
}
</script>
