<template>
  <div :class="{ 'focused': hasData }" class="input-group textarea">
    <label @click="focusInput">{{ label }}</label>
    <textarea
      ref="inputElement"
      name="notes"
      cols="30"
      rows="5"
      :value="localValue"
      @keydown.self="focusInput"
      @input="handleInput($event)"
    />
    <CompleteIcon :display="isComplete" />
  </div>
</template>

<script>
import { inputValidation } from '../../mixins/inputValidation'
import CompleteIcon from './fragments/CompleteIcon'

export default{
  name: 'TextareaInput',
  mixins: [inputValidation],
  components: {
    CompleteIcon
  },
  data () {
    return {
      localValue: ''
    }
  },
  mounted () {
    if (this.$store.getters.getNotes) {
      this.localValue = this.$store.getters.getNotes
    }
    this.isComplete = this.localValue.length > 0 && !this.hasError
  },
  methods: {
    focusInput () {
      this.isFocused = true
      this.$nextTick(() => this.$refs.inputElement.focus())
    },
    handleInput (e) {
      this.localValue = e.target.value
      this.emitInput(e.target.value)
      this.validate()
    }
  }
}
</script>
