<template>
  <section class="inner-section">
    <h2 class="section-title">Customer details</h2>
    <div :value="hasName" class="form-section">
      <TextInput
        :name="'name'"
        :label="'Name'"
        :hint="'Enter your first and last name'"
        v-model="localName"
      />
    </div>
    <div :value="hasEmail" class="form-section">
      <EmailInput
        :name="'email'"
        :label="'Email'"
        :input-type="'email'"
        :validation="'email'"
        v-model="localEmail"
      />
    </div>
    <div :value="hasPhone" class="form-section">
      <PhoneInput
        :name="'phone'"
        :label="'Phone Number'"
        :input-type="'phone'"
        :validation="'phone'"
        v-model="localPhone"
      />
    </div>
    <div class="form-section">
      <DesktopCTA :can-proceed="canProceed"/>
    </div>
  </section>
</template>

<script>
import TextInput from '../../components/inputs/TextInput.vue'
import EmailInput from '../../components/inputs/EmailInput.vue'
import PhoneInput from '../../components/inputs/PhoneInput.vue'
import DesktopCTA from './DesktopCTA.vue'

export default {
  name: 'Details',
  components: {
    TextInput,
    EmailInput,
    PhoneInput,
    DesktopCTA
  },
  data () {
    return {
      localName: this.$store.getters.getClientName,
      localEmail: this.$store.getters.getClientEmail,
      localPhone: this.$store.getters.getClientPhone
    }
  },
  computed: {
    hasName () {
      let hasName = this.localName || this.$store.getters.getClientName
      if (hasName) {
        this.$store.dispatch('saveName', hasName)
      }
      return hasName
    },
    hasEmail () {
      let hasEmail = this.localEmail || this.$store.getters.getClientEmail
      if (hasEmail) {
        this.$store.dispatch('saveEmail', hasEmail)
      }
      return hasEmail
    },
    hasPhone () {
      let hasPhone = this.localPhone || this.$store.getters.getClientPhone
      if (hasPhone) {
        this.$store.dispatch('savePhone', hasPhone)
      }
      return hasPhone
    },
    //  Use this to reactively change formProceed value once all fields are filled
    canProceed () {
      if (this.localName && this.localEmail && this.localPhone) {
        
        this.$store.dispatch('saveName', this.localName)
        this.$store.dispatch('saveEmail', this.localEmail)
        this.$store.dispatch('savePhone', this.localPhone)

        this.$store.dispatch('changeFormProceed', true)
        return true
      } else {
        this.$store.dispatch('changeFormProceed', false)
        return false
      }
    }
  },
  created () {
    if (!this.$store.getters.getCurrentStep) { this.$router.push('/') }
    this.$store.dispatch('changeCurrentStep', 'stepTwo')
  }
}
</script>
