/** Mixin that provides some overall helpers */
import moment from 'moment'
const helpers = {
  computed: {
    formatedDate (){
      return (this.$store.getters.getDate ? moment(this.$store.getters.getDate).format('dddd, DD MMM YYYY') : '')
    },
    fomatedTime () {
      let slot = this.$store.getters.getTime
      if (slot) {
        return this.formatTime(slot)
      } else {
        return ''
      }
    }
  },
  methods: {
    formatTime (slot){
      let dif = moment(slot.finish_time).diff(slot.start_time, 'hours')
      return moment(slot.start_time).format('HH:mm') + ' to ' + moment(slot.finish_time).format('HH:mm') + ' (' + dif + 'h)'
    }
  }
}

export { helpers }
