<template>
  <div :class="{ 'focused': hasData, 'error': displayError }" class="input-group">
    <label @click="focusInput" v-html="buildLabel" />
    <input
      ref="inputElement"
      tabindex="1"
      :value="localValue"
      :name="name"
      :type="inputType"
      @blur="unfocusInput($event)"
      @input="focusInput($event)"
    >
    <CompleteIcon :display="isComplete" />
    <InputInfoSection
      :has-hint="hasHint"
      :hint="hint"
      :display-error="displayError"
      :error-value="errorValue"
    />
  </div>
</template>

<script>
import { inputValidation } from '../../mixins/inputValidation'
import CompleteIcon from './fragments/CompleteIcon'
import InputInfoSection from './fragments/InputInfoSection'

export default{
  name: 'EmailInput',
  mixins: [ inputValidation ],
  components: {
    CompleteIcon,
    InputInfoSection
  },
  data () {
    return {
      localValue: ''
    }
  },
  mounted () {
    if (this.$store.getters.getClientEmail) {
      this.localValue = this.$store.getters.getClientEmail
    }
    this.isComplete = this.localValue.length > 0 && !this.hasError
  },
  methods: {
    focusInput (e) {
      this.localValue = e.target.value
      this.$nextTick(() => this.$refs.inputElement.focus())
      this.clearError()
      if (this.isValidEmail()) {
        if (this.validate()) {
          this.$emit('input', e.target.value)
        } else {
          this.$emit('input', false)
        }
      } else {
       this.setComplete(false) 
      }
    },
    unfocusInput (e) {
      if (this.validate()) {
        this.$emit('input', e.target.value)
      } else {
        this.$emit('input', false)
      }
    }
  }
}
</script>
