const extrasModule = {
  state:{
    needShuttle: false,
    address: false,
    room: false,
    shuttlePax: null,
    shuttleType: 'roundtrip',
    notes: false    
  },
  mutations:{
    setShuttle (state, value) { state.needShuttle = value },
    setShuttlePax (state, value) { state.shuttlePax = value },
    setAddress (state, value) { state.address = value },
    setRoom (state, value) { state.room = value },
    setShuttleType (state, value) { state.shuttleType = value },
    setNotes (state, value) { state.notes = value }
  },
  actions:{
    saveShuttle ({ commit }, value) { commit('setShuttle', value) },
    saveShuttlePax ({ commit }, value) { commit('setShuttlePax', value) },
    saveAddress ({ commit }, value) { commit('setAddress', value) },
    saveRoom ({ commit }, value) { commit('setRoom', value) },
    saveShuttleType ({ commit }, value) { commit('setShuttleType', value) },
    saveNotes ({ commit }, value) { commit('setNotes', value) }
  },
  getters: {
    getShuttle: state => { return state.needShuttle },
    getShuttlePax: state => { return state.shuttlePax },
    getAddress: state => { return state.address },
    getRoom: state => { return state.room },
    getShuttleType: state => { return state.shuttleType },
    getNotes: state => { return state.notes },
  }
}

export default extrasModule
