const customerModule = {
  state:{
    clientName: false,
    clientEmail: false,
    clientPhone: false,
  },
  mutations:{
    setSelectedName (state, name) { state.clientName = name },
    setSelectedEmail (state, email) { state.clientEmail = email },
    setSelectedPhone (state, phone) { state.clientPhone = phone }    
  },
  actions:{
    saveName ({ commit }, name) { commit('setSelectedName', name) },
    saveEmail ({ commit }, email) { commit('setSelectedEmail', email) },
    savePhone ({ commit }, phone) { commit('setSelectedPhone', phone) }
  },
  getters: {
    getClientName: state => { return state.clientName },
    getClientEmail: state => { return state.clientEmail },
    getClientPhone: state => { return state.clientPhone }
  }
}

export default customerModule
