<template>
  <div :class="{ 'expanded': isExpanded }" class="inner-section summary">
    <div class="header-section" @click="toggleSummary">
      <h2 class="section-title">Summary</h2>
      <small :class="{ 'hide': hasInteracted }" class="reveal-hint">Tap to collapse</small>
      <span v-if="isExpanded" class="collapse-toggle">
        <font-awesome-icon icon="chevron-up" />
      </span>
      <span v-else class="collapse-toggle">
        <font-awesome-icon icon="chevron-down" />
      </span>
    </div>
    <div class="card white">
      <div class="card-inner">
        <h2 class="title">{{ activityTitle }}</h2>
        <h4 class="subtitle">{{ formatedDate }}</h4>
        <h4 class="subtitle">{{ fomatedTime }}</h4>
      </div>
      <div class="card-inner pt-0">
        <div class="details">
          <span v-for="price in prices" class="detail">
            <span v-if="price.selectedPax > 0">{{ price.selectedPax }} {{ price.label }} <span v-html="formatPrice(price)" /></span></span>
        </div>
      </div>
      <div v-if="hasDetails" class="card-inner pt-0">
        <div class="details">
          <div class="detail alt">
            <span class="label">Name</span>
            <span class="value">{{ name }}</span>
          </div>
          <div class="detail alt">
            <span class="label">Email</span>
            <span class="value">{{ email }}</span>
          </div>
          <div class="detail alt">
            <span class="label">Phone</span>
            <span class="value">{{ phone }}</span>
          </div>
        </div>
      </div>
      <div v-if="needsShuttle" class="card-inner pt-0">
        <div class="details">
          <div class="detail alt half">
            <span class="label">Requires Shuttle</span>
            <span class="value">{{ shuttleType }}</span>
          </div>
          <div class="detail alt half">
            <span class="label">Passengers</span>
            <span class="value">{{ shuttlePax }}</span>
          </div>
          <div class="detail alt">
            <span class="label">Pickup/dropoff Address</span>
            <span class="value">{{ selectedAddress }}</span>
          </div>
          <div v-if="selectedRoom" class="detail alt">
            <span class="label">Room</span>
            <span class="value">{{ selectedRoom }}</span>
          </div>
        </div>
      </div>
      <div v-if="hasPrices" class="card-inner pt-0">
        <div class="details">
          <span class="detail price">
            <span class="price-label" >Total</span>
            <span class="price-label" v-html="getTotal.label" />
          </span>
          <span class="detail price">
            <span class="price-label" >Deposit</span>
            <span class="price-label" v-html="deposit.label" />
          </span>
          <span class="detail price">
            <span class="price-label" >Due upon check-in</span>
            <span class="price-label" v-html="dueCheckin" />
          </span>
        </div>
      </div>
      <div v-if="notes" class="card-inner pt-0">
        <div class="details">
          <div class="detail alt">
            <span class="label">Additional Notes</span>
            <span class="value">{{ notes }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { helpers } from '../../mixins/helpers'

export default {
  name: 'Summary',
  mixins: [helpers],
  data () {
    return {
      isExpanded: true,
      hasInteracted: false,
      total: 0
    }
  },
  computed: {
    activityTitle () {
      return this.$store.getters.getActivityTitle
    },
    prices (){ return this.$store.getters.getPrices },
    hasPrices (){ return Object.keys(this.prices).length > 0 },
    hasDetails () {
      if (this.$store.getters.getClientName || this.$store.getters.getClientEmail || this.$store.getters.getClientPhone) {
        return true
      } else {
        return false
      }
    },
    name () { return this.$store.getters.getClientName ? this.$store.getters.getClientName : 'N/A' },
    email () { return this.$store.getters.getClientEmail ? this.$store.getters.getClientEmail : 'N/A' },
    phone () { return this.$store.getters.getClientPhone ? this.$store.getters.getClientPhone : 'N/A' },
    needsShuttle () { return this.$store.getters.getShuttle },
    shuttleType () {
      if (this.$store.getters.getShuttleType === 'roundtrip') { return 'Roundtrip' }
      if (this.$store.getters.getShuttleType === 'oneway-to') { return 'To Go' }
      if (this.$store.getters.getShuttleType === 'oneway-from') { return 'To Return' }
    },
    shuttlePax () { return this.$store.getters.getShuttlePax ? this.$store.getters.getShuttlePax : 'N/A'},
    selectedAddress () { return this.$store.getters.getAddress ? this.$store.getters.getAddress : 'N/A'},
    selectedRoom (){ return this.$store.getters.getRoom ? this.$store.getters.getRoom : false },
    notes () { return this.$store.getters.getNotes ? this.$store.getters.getNotes : false },
    getTotal () {
      if (this.hasPrices > 0) {
        let prices = this.prices 
        let total = 0
        for (let key in prices){
          total += prices[key].selectedPax * (prices[key].value_cents / 100)
        }
        this.total = total

        this.$store.dispatch('saveTotal', total)

        return {
          value: total,
          label: ` ${ total }`
        }
      } else {
        return 0
      }
    },
    deposit (){
      let value = this.total * 0.1
      this.$store.dispatch('saveFee', value)
      return {
        value: value,
        label: ` ${ value }`
      }
    },
    dueCheckin () { return ` ${ this.total - this.deposit.value }` }
  },
  methods: {
    toggleSummary () {
      this.hasInteracted = true
      this.isExpanded = !this.isExpanded
    },
    formatPrice (selectedPrice){
      let price = selectedPrice.value_cents / 100
      let total = selectedPrice.selectedPax * price
      return `- ${ total } <small>(${ price } / pax)</small>`
    }
  }
}
</script>
