<template>
  <div>
    <nav :class="currentStep" class="tab-navigation">
      <a href="https://seabookings.com" class="logo-wrapper">
        <SBLogo class="logo" />
      </a>
      <div class="timeline">
        <NavLink v-for="(link, index) in links" :key="index" :text="link.text" :path="link.path" :link-class="link.linkClass" :current-index="index" />
        <div class="progress" />
      </div>
    </nav>
    <div :class="currentStep" class="mobile-timeline" >
      <div :class="{ 'is-disabled': !canProceed, 'is-payment': finalStep }" class="progress">
        <router-link :content="stepLabel" :to="nextStep" :disabled="canProceed === false" class="btn next-mobile" tag="button" />
        <span class="steps">{{ currentStepNumber }}/{{ totalSteps }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import NavLink from './NavLink'
import SBLogo from './SBLogo'

export default{
  name: 'Navigation',
  components: {
    NavLink,
    SBLogo
  },
  data () {
    return {
      links: this.$store.getters.getNavLinks
    }
  },
  computed: {
    canProceed () { return this.$store.getters.canProceed },
    currentStepNumber () { return this.$store.getters.getCurrentStep.stepNumber },
    totalSteps () { return this.links.length },
    finalStep () { return this.currentStepNumber === this.totalSteps },
    stepLabel () {
      let total = this.$store.getters.getFee
      return !this.finalStep ? 'Next' : `Pay now  ${ total } (deposit)`
    },
    nextStep () {
      if (this.$store.getters.getCurrentStep) {
        return this.$store.getters.getCurrentStep.nextPath
      } else {
        return ''
      }
    },
    currentStep () { return this.$store.getters.getCurrentStep.stepClass }
  }
}
</script>

