import Vue from 'vue'
import App from './App.vue'
import store from './store/store'
import Toasted from 'vue-toasted'
import VueRouter from 'vue-router'
import setupInterceptors from './interceptors'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faChevronUp, faChevronDown } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* Router components */
import Booking from './components/bookingSections/Booking.vue'
import Details from './components/bookingSections/Details'
import Extras from './components/bookingSections/Extras'
import Payment from './components/bookingSections/Payment'
import PaymentSuccess from './components/bookingSections/PaymentSuccess'
import ProcessPayment from './components/bookingSections/ProcessPayment'
import NotFound from './components/NotFound'

// require('dotenv').config()

Vue.use(Toasted, {
  position: 'bottom-right',
  duration: 5000
})

Vue.use(VueRouter)

library.add(faChevronUp)
library.add(faChevronDown)

Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = process.env.NODE_ENV === 'production'

setupInterceptors()

const routes = [
  {
    path: '/',
    name: 'booking',
    component: Booking
  },
  {
    path: '/customer-details',
    name: 'customerDetails',
    component: Details
  },
  {
    path: '/extras',
    name: 'bookingExtas',
    component: Extras
  },
  {
    path: '/payment',
    name: 'bookingPayment',
    component: Payment
  },
  {
    path: '/process-payment',
    name: 'processPayment',
    component: ProcessPayment
  },
  {
    path: '/payment-success',
    name: 'paymentSuccess',
    component: PaymentSuccess
  },
  {
    path: '/not-found',
    name: 'notFound',
    component: NotFound
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')
