import Vue from 'vue'
import Vuex from 'vuex'
import apiModule from './modules/apiModule'
import bookingModule from './modules/bookingModule'
import customerModule from './modules/customerModule'
import extrasModule from './modules/extrasModule'
import paymentModule from './modules/paymentModule'

Vue.use(Vuex)

export default new Vuex.Store({
  modules:{
    api: apiModule,
    booking: bookingModule,
    customer: customerModule,
    extras: extrasModule,
    payment: paymentModule
  },
  state: {
    /* Form config */
    appIsLoading: true,
    loadingMessage: 'Loading activity',
    currentPageClass: 'step-one',
    canProceedNext: false,
    formSteps: {
      stepOne: {
        stepNumber: 1,
        name: 'Booking',
        stepClass: 'step-one',
        prevPath: '',
        currentPath: '/',
        nextPath: '/customer-details'
      },
      stepTwo: {
        stepNumber: 2,
        name: 'Details',
        stepClass: 'step-two',
        prevPath: '/',
        currentPath: '/customer-details',
        nextPath: '/extras'
      },
      stepThree: {
        stepNumber: 3,
        name: 'Extras',
        stepClass: 'step-three',
        prevPath: '/customer-details',
        currentPath: '/extras',
        nextPath: '/payment'
      },
      stepFour: {
        stepNumber: 4,
        name: 'Payment',
        stepClass: 'step-four',
        prevPath: '/extras',
        currentPath: '/payment',
        nextPath: ''
      }
    },
    currentStep: false,
  },
  mutations: {
    setAppLoading (state, value){ state.appIsLoading = value },
    setLoadingMessage (state, value){ state.loadingMessage = value },
    setPageClass (state, className) { state.currentPageClass = className },
    setCurrentStep (state, step) { state.currentStep = step },
    setFormProceed (state, canProceed) { state.canProceedNext = canProceed }
  },
  actions: {
    changePageClass ({ commit }, className) { commit('setPageClass', className) },
    changeCurrentStep ({ commit }, stepName) { commit('setCurrentStep', this.getters.getFormStep(stepName)) },
    changeFormProceed ({ commit }, canProceed) { commit('setFormProceed', canProceed) },
  },
  getters: {
    getCurrentPageClass: state => { return state.currentPageClass },
    getNavLinks: (state) => {
        let steps = []
        Object.keys(state.formSteps).forEach((key) => {
            let step = state.formSteps[key]
            steps.push({
                text: step.name,
                linkClass: step.stepClass,
                path: step.currentPath
            })
        })
        return steps
    },
    appIsLoading: state =>{ return state.appIsLoading },
    getLoadingMessage: state =>{ return state.loadingMessage },
    getFormStep: (state) => (stepName) => { return state.formSteps[stepName] },
    getTotalSteps: (state, getters) => { return getters.getNavLinks.length },
    getCurrentStep: state => { return state.currentStep },
    canProceed: state => { return state.canProceedNext }
  }
})

