import Vue from 'vue'
import axios from 'axios'

const setupInterceptors = () =>{
  axios.interceptors.response.use(response => {
     return response;
  }, error => {
    if (error.response.status === 401) {
     Vue.toasted.error('Unauthorized service connection.');
    }
    return error;
  });
}

export default setupInterceptors
