const bookingModule = {
  state: {
    activityId: '',
    activitySlug: '',
    activityTitle: '',
    availabilities: {},
    activitySlots: [],
    maxCapacity: 1,
    date: false,
    time: false,
    adultPax: null,
    prices: {},
    totalPrice: 0,
    fee: 0
  },
  mutations: {
    setActivityId (state, id) { state.activityId = id },
    setActivitySlug (state, slug) { state.activitySlug = slug },
    setActivityTitle (state, title) { state.activityTitle = title },
    setAvailabilities (state, data) { state.availabilities = data },
    setSlots (state, data) { state.activitySlots = data },
    setSelectedDate (state, date) { state.date = date },
    setSelectedTime (state, time) { state.time = time },
    setCapaticy (state, value) { state.maxCapacity = value },
    updatePax (state, data){ state.prices[data.id]['selectedPax'] = data.pax },
    setPrice (state, value) { state.totalPrice = value },
    setFee (state, value) { state.fee = value },
    /** Deprec */
    setPrices (state, value) { state.prices = value }
  },
  actions: {
    saveActivityData ({ commit }, data) {
      commit('setActivityId', data['id'])
      commit('setActivitySlug', data['slug'])
      commit('setActivityTitle', data['name'])
    },
    saveAvailabilities ({ commit }, data) {
      let availabilities = {}
      for (var key in data) {
        if (data[key].length > 0) {
          availabilities[key] = data[key]
        }
      }
      commit('setAvailabilities', availabilities)
    },
    saveSlots ({ commit }, data) {
      let slots = []
      for (var key in data) {
        let slot = data[key].attributes
        slot['prices'] = data[key].relationships.prices.data
        slots.push(slot)
      }
      commit('setSlots', slots.sort(function(a, b){return new Date(a.start_time) - new Date(b.start_time)}))
    },
    savePrices ({ commit }, data) {
      let prices = {}
      for (var key in data) {
        data[key].attributes['selectedPax'] = 0
        prices[data[key].id] = data[key].attributes
      }
      commit('setPrices', prices)
    },
    saveSelectedDate ({ commit }, date) { commit('setSelectedDate', date) },
    saveSelectedTime ({ commit }, time) {
      commit('setSelectedTime', time)
      commit('setCapaticy', time.capacity)
    },
    saveTotal ({ commit }, value){ commit('setPrice', value) },
    saveFee ({ commit }, value){ commit('setFee', value) },
    /** Deprec */
    saveAdultPax ({ commit }, value) { commit('setAdultPax', value) }
  },
  getters: {
    getActivityId: state => { return state.activityId },
    getActivityTitle: state => { return state.activityTitle },
    getDate: state => { return state.date },
    getAvailabilities: state => { return state.availabilities },
    getSlots: state => { return state.activitySlots },
    getTime: state => { return state.time },
    getMaxCapacity: state => { return state.maxCapacity },
    getPrices: state => { return state.prices },
    getPrice: (state) => (id) => { return state.prices[id] },
    getTotal: state => { return state.totalPrice },
    getFee: state => { return state.fee },
  }
}

export default bookingModule
