<template>
  <div :class="{ 'is-selecting': isSelecting, 'focused': hasData, 'error': displayError }" class="input-group">
    <div v-if="isLoading" class="loader in-input"><span/></div>
    <label @click="handleInput" v-html="buildLabel" />
    <input
      ref="inputElement"
      :disabled="isDisabled || isLoading"
      :value="localValue"
      :type="inputType"
      @focus="handleInput"
      @blur="unfocusInput"
      @keydown.prevent="preventInput"
    >
    <CompleteIcon :display="isComplete" />
    <InputInfoSection
      :has-hint="hasHint"
      :hint="hint"
      :display-error="displayError"
      :error-value="errorValue"
    />
    <div class="dropdown-wrapper">
      <ul>
        <li v-for="slot in timeslots" :key="slot.slot_id"><a :id="slot.slot_id" href="#" @click="selectTime(slot)" v-text="formatTime(slot)" /></li>
      </ul>
    </div>
  </div>
</template>

<script>
import { inputValidation } from '../../mixins/inputValidation'
import { helpers } from '../../mixins/helpers'
import CompleteIcon from './fragments/CompleteIcon'
import InputInfoSection from './fragments/InputInfoSection'
import moment from 'moment'

export default {
  name: 'TimeInput',
  components: {
    CompleteIcon,
    InputInfoSection
  },
  mixins: [ helpers, inputValidation ],
  props: {
    timeslots: {
      type: Array,
      default: () => { return [] }
    }
  },
  data () {
    return {
      localValue: '',
    }
  },
  computed: {
    formatedDate(){
      if (this.$store.getters.getTime) {
        let slot = this.$store.getters.getTime
        this.setComplete(true)
        return this.formatTime(slot)
      } else {
        return ''
      }
    }
  },
  mounted () { this.localValue = this.formatedDate },
  methods: {
    handleInput () {
      this.isSelecting = true
      this.$nextTick(() => this.$refs.inputElement.focus())
    },
    unfocusInput () {
      setTimeout(() => {
        this.isSelecting = false
        this.isFocused = this.hasData
      }, 250)
    },
    selectTime (slot) {
      this.localValue = this.formatTime(slot)
      this.isComplete = true
      this.$emit('input', slot)
    },
    preventInput (e) { e.preventDefault() }
  }
}
</script>
