<template>
  <div>
    <form method="post" id="payment-form">
      <div class="form-row">
        <TextInput
          :name="'name'"
          :label="'Name'"
          v-model="localName"
        />
      </div>
      <div class="form-row">
        <div :class="{ 'error': displayError }" class="input-group">
          <div :id="elementId"></div>
          <CompleteIcon :display="completeCard" />
          <InputInfoSection
            :display-error="displayError"
            :error-value="errorValue"
          />
          <div class="powered-by">
            <StripeBadge />
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import StripeBadge from './StripeBadge'
import TextInput from '../../inputs/TextInput'
import CompleteIcon from '../../inputs/fragments/CompleteIcon'
import InputInfoSection from '../../inputs/fragments/InputInfoSection'
import stripeOptions from './stripeOptions'

const stripe = Stripe(process.env.VUE_APP_STRIPE_PUBLISHABLE_KEY)
const elements = stripe.elements()

export default {
  name: 'CardForm',
  components: {
    StripeBadge,
    TextInput,
    CompleteIcon,
    InputInfoSection
  },
  props:{
    proceedPayment:{
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      elementId: 'card-element',
      localName: this.$store.getters.getClientName,
      completeCard: false,
      displayError: false,
      errorValue: '',
      cardRef: null
    }
  },
  watch:{
    proceedPayment () {
      if (this.proceedPayment){
        this.$store.commit('setAppLoading', true)
        this.$store.commit('setLoadingMessage', 'Authorizing payment')

        let ownerInfo = {
          owner: {
            name: this.$store.getters.getClientName,
            email: this.$store.gettersgetClientEmail
          }
        }

        //  For testing purpose. Remove when not needed anymore
        //  4000 0000 0000 3063 // Required 3D
        //  4000 0000 0000 3055 // Optional 3D
        //  3782 8224 6310 005  // Not supported

        console.log(process.env)

        stripe.createSource(this.cardRef, ownerInfo)
          .then((result) => {
            if (result.error) {
              this.$toasted.error('Error getting token!')
              console.log(err)
            } else {
              // Send the source to your server
              this.$toasted.success('Got response')
              console.log(result.source)
              
              if (result.source.card.three_d_secure !== 'not_supported'){
                this.$toasted.success('Process using 3D Secure')
                stripe.createSource({
                  type: 'three_d_secure',
                  amount: 1099,
                  currency: 'eur',
                  three_d_secure: {
                    card: result.source.id
                  },
                  redirect: {
                    return_url: `${ process.env.VUE_APP_URL }/process-payment`
                  }
                }).then((result) => {
                  console.log(result.source)
                  if (result.error){
                    this.$toasted.error('Error creating 3D source!');
                    console.log(result.error)
                  } else {
                    if (result.source.status !== 'chargeable'){
                      this.$store.commit('setLoadingMessage', 'Redirecting to 3D Secure page')
                      window.location = result.source.redirect.url
                    } else{
                      this.$store.commit('setAppLoading', false)
                      this.$store.commit('setLoadingMessage', '')
                      alert('Send source to API for server charging')
                    }
                  }
                })

              } else {
                this.$toasted.success('Process card regularly')
                this.$store.commit('setAppLoading', false)
                this.$store.commit('setLoadingMessage', '')
                alert('Send source to API for server charging')
              }
            }
          })
          .catch(err =>{
            this.$toasted.error('Error getting token!');
            console.log(err)
          })
        
        // stripe
        // .createToken(this.cardRef)
        // .then((result) => {
        //   this.$store.commit('setAppLoading', false)
        //   this.$store.commit('setLoadingMessage', '')
        //   this.$toasted.success('Got token');
        //   console.log(result)
        // })
        // .catch(err =>{
        //   this.$toasted.error('Error getting token!');
        //   console.log(err)
        // })
      }
    }
  },
  mounted (){
    let card = elements.create('card', stripeOptions)
    
    card.mount(`#${ this.elementId }`)

    //Keep ref to destroy element when destroying component
    this.cardRef = card

    card.on('change', (data) =>{
      let proceed = false
      if (typeof data.error !== 'undefined'){
        this.displayError = true
        this.errorValue = data.error.message
      } else{
        this.displayError = false
        this.errorValue = ''
        if (data.complete){
          proceed = true
        }
        this.completeCard = proceed
      }
      this.$store.dispatch('changeFormProceed', proceed)
    })
  },
  beforeDestroy (){
    this.cardRef.destroy()
  }
}
</script>
