/** Mixin to manipulate input style, label & error message/state */
const inputStyler = {
  props: {
    label: {
      type: String,
      default: 'Label'
    },
    hint: {
      type: String,
      default: ''
    },
    isLoading: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isFocused: false,
      isSelecting: false,
      isComplete: false,
      displayError: false,
      hasError: false,
      valid: false,
      errorValue: ''
    }
  },
  computed: {
    hasData () {
      let hasData = false
      switch (this.inputType) {
        case 'text':
        case 'email':
        case 'phone':
          hasData = this.localValue.length > 0 || this.tmpValue
          break
        case 'number':
          /** Not ideal, as when user adds and removes input, it becomes NaN (number input doesn't accepts empty string or false, and we don't want to initialize with zero..) */
          hasData = ((this.localValue !== null && !isNaN(this.localValue)) ? this.localValue >= 0 : false)
          break
      }
      return hasData
    },
    buildLabel () {
      let tmpLabel = this.label
      if (this.name === 'adults' || this.name === 'children' || this.name === 'spectator') {
        tmpLabel = this.label + this.formatedPrice
      }
      return (this.isRequired || this.requiresPrevInput ? tmpLabel + ' <sup>*</sup>' : tmpLabel)
    },
    formatedPrice () {
      return (this.inputPrice > 0 ? ' ' + (this.inputPrice / 100) : '' )
    },
    hasHint () { return this.hint.length > 0 }
  },
  methods: {
    setComplete (complete) { this.isComplete = complete },
    setError (message) {
      this.hasError = true
      this.displayError = true
      this.errorValue = message
      this.setComplete(false)
    },
    clearError () {
      this.hasError = false
      this.displayError = false
      this.errorValue = ''
    }
  }
}

export { inputStyler }
