<template>
  <div>
    <button v-if="finalStep" :class="{ 'is-disabled': !canProceed }" :content="stepLabel" :to="nextStep" :disabled="canProceed === false" class="btn btn-primary desktop-cta is-payment" tag="button" @click="emitClick" />
    <router-link v-else :class="{ 'is-disabled': !canProceed, 'is-payment': finalStep }" :content="stepLabel" :to="nextStep" :disabled="canProceed === false" class="btn btn-primary desktop-cta" tag="button" />
  </div>
</template>

<script>
export default {
  name: 'DesktopCTA',
  props: {
    canProceed: {
      type: Boolean,
      default: false
    },
    customLabel:{
      type: String,
      default: 'Pay now'
    }
  },
  computed: {
    stepLabel () {
      let total = this.$store.getters.getFee
      return !this.finalStep ? 'Next' : `${ this.customLabel }  ${ total } (deposit)`
    },
    nextStep () {
      if (this.$store.getters.getCurrentStep) {
        return this.$store.getters.getCurrentStep.nextPath
      } else {
        return ''
      }
    },
    finalStep () {
      return this.$store.getters.getCurrentStep.stepNumber === this.$store.getters.getTotalSteps
    }
  },
  methods: {
    emitClick () {
      this.$emit('click')
    }
  }
}
</script>
