<template>
  <router-link :class="[linkClass, { 'current': isCurrent, 'disabled': !canProceed, 'complete': isComplete }]" class="tab-link" event="" :to="path" @click.native.prevent="navigate(path)">{{ text }}</router-link>
</template>

<script>
export default{
  name: 'NavLink',
  props: {
    text: {
      type: String,
      default: 'Link'
    },
    path: {
      type: String,
      default: '/'
    },
    linkClass: {
      type: String,
      default: ''
    },
    currentIndex: {
      type: Number,
      default: 0
    }
  },
  computed: {
    canProceed () {
      if (this.currentIndex + 1 <= this.$store.getters.getCurrentStep.stepNumber) {
        return true
      } else if (this.currentIndex + 1 === this.$store.getters.getCurrentStep.stepNumber + 1 && this.$store.getters.canProceed) {
        return true
      } else {
        return false
      }
    },
    isComplete () {
      return (this.currentIndex + 1 < this.$store.getters.getCurrentStep.stepNumber) || (this.currentIndex + 1 === this.$store.getters.getCurrentStep.stepNumber && this.$store.getters.canProceed)
    },
    isCurrent () {
      return this.linkClass === this.$store.getters.getCurrentStep.stepClass
    }
  },
  methods: {
    navigate (path) {
      if (this.canProceed) {
        this.$router.push(path)
      }
    }
  }
}
</script>
