<template>
  <div class="main-page">
    <div v-if="appLoading" class="page-loader">
      <div class="load-wrapper">
        <div class="loader"><span/></div>
        {{ loadingMessage }}
      </div>
    </div>
    <Navigation />
    <div :class="{ 'final-section': finalStep }" class="section-row">
      <div class="booking-section">
        <transition name="fade" mode="out-in">
          <router-view/>
        </transition>
      </div>
      <div class="section summary-section">
        <Summary />
      </div>
    </div>
  </div>
</template>

<script>
import Navigation from './components/navigation/Navigation.vue'
import Summary from './components/bookingSections/Summary.vue'
import moment from 'moment'

const initiateApp = (app) => {

  let q = app.$router.currentRoute.query

  if ('client_secret' in q && 'source' in q) {
    app.$store.commit('setAppLoading', false)
    app.$router.push('/process-payment')
    return false
  }

  app.$store.dispatch('getAuthToken').then((res, err) => {
    
    if ('activity' in q) { return q.activity
    } else {
      return false
    }
  })
  .then(id => {
    if (id) {

      app.$store.dispatch('getActivity', id).then(res => {
        if(res.data){
          app.$store.dispatch('saveActivityData', res.data.data.attributes)
        }
      })
      .catch(err => {
        console.log('Error')
        console.log(err)
      })
      return id
    } else {
      app.$store.commit('setAppLoading', false)
      app.$router.push('/not-found')
      return false
    }
  })
  .then(id => {
    if (id) {
      let start = moment().format('YYYY-MM-DD')
      let end = moment().endOf('month').format('YYYY-MM-DD')
      app.$store.dispatch('getAvailabilities', {
        id: id, 
        start: start,
        end: end
      }).then(res => {
        app.$store.dispatch('saveAvailabilities', res.data.data.attributes)
        app.$store.commit('setAppLoading', false)
      })
    }
  })
  .catch(err => {
    app.$toasted.error('Error connecting to service')
    console.log(err)
  })
}

export default {
  name: 'App',
  components: {
    Navigation,
    Summary
  },
  computed: {
    appLoading (){ return this.$store.getters.appIsLoading },
    loadingMessage () { return this.$store.getters.getLoadingMessage },
    finalStep () {
      return this.$store.getters.getCurrentStep.stepNumber === this.$store.getters.getTotalSteps
    }
  },
  created () {
    initiateApp(this)
  },
}
</script>

