// Loader for inputs
.loader-wrapper{
  display: none;
  position: absolute;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 85%;
  margin-top: 15%;
  background: rgba(255, 255, 255, .8);
  z-index: 1;
  &.visible{
    display: flex;
  }
}
.loader{
  display: block;
  height: 32px;
  width: 32px;
  -webkit-animation: loader-1 3s linear infinite;
          animation: loader-1 3s linear infinite;
  span{
    display: block;
    position: absolute;
    top: 0; left: 0;
    bottom: 0; right: 0;
    margin: auto;
    height: 32px;
    width: 32px;
    clip: rect(16px, 32px, 32px, 0);
    -webkit-animation: loader-2 1.5s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
            animation: loader-2 1.5s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
    &::before{
      content: "";
      display: block;
      position: absolute;
      top: 0; left: 0;
      bottom: 0; right: 0;
      margin: auto;
      height: 20px;
      width: 20px;
      border: 3px solid transparent;
      border-top: 3px solid $primary;
      border-radius: 50%;
      -webkit-animation: loader-3 1.5s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
              animation: loader-3 1.5s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
    }
    &::after{
      content: "";
      display: block;
      position: absolute;
      top: 0; left: 0;
      bottom: 0; right: 0;
      margin: auto;
      height: 20px;
      width: 20px;
      border: 3px solid $primary-trans;
      border-radius: 50%;
    }
  }
}
@-webkit-keyframes loader-1 {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes loader-1 {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
@-webkit-keyframes loader-2 {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes loader-2 {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
@-webkit-keyframes loader-3 {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes loader-3 {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
