<template>
  <div class="form-check">
    <input
      class="form-check-input"
      type="radio"
      :name="name"
      :id="value"
      :value="value"
      v-model.lazy="localValue"
      @change="saveOption"
    >
    <label :class="value+'-label'" class="form-check-label" :for="value">
      <CardIcon v-if="value === 'card'" />
      <PayPalIcon v-else-if="value === 'paypal'" />
      <img src="@/assets/ideal.gif" v-else-if="value === 'ideal'" />
      {{ label }} </label>
  </div>
</template>

<script>
import CardIcon from './fragments/CardIcon'
import PayPalIcon from './fragments/PayPalIcon'

export default {
  name: 'RadioInput',
  components: {
    CardIcon,
    PayPalIcon
  },
  props: {
    label: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: 'unknown'
    },
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localValue: ''
    }
  },
  mounted () {
    if (this.name === 'transferType') {
      this.localValue = this.$store.getters.getShuttleType
    }
    if (this.name === 'paymentMethod') {
      this.localValue = this.$store.getters.paymentOption
    }
  },
  methods: {
    saveOption () {
      if (this.name === 'transferType') {
        this.$store.dispatch('saveShuttleType', this.localValue)
      }
      if (this.name === 'paymentMethod') {
        this.$store.commit('setPaymentOption', this.localValue)
      }
      
    }
  }
}
</script>
