<template>
  <div :class="{ 'is-selecting': isSelecting, 'focused': hasData, 'error': displayError }" class="input-group">
    <label @click="handleFocus" v-html="buildLabel" />
    <input
      ref="inputElement"
      :type="inputType"
      :value="formatedDate"
      :name="name"
      readonly="true"
      @focus="handleFocus"
      @blur="unfocusInput"
      @keydown.prevent="preventKeypress"
    >
    <CompleteIcon :display="isComplete" />
    <InputInfoSection
      :has-hint="hasHint"
      :hint="hint"
      :display-error="displayError"
      :error-value="errorValue"
    />
    <div class="calendar-wrapper">
      <div :class="{ 'visible': calendarLoading }" class="loader-wrapper">
        <div class="loader"><span /></div>
      </div>
      <datepicker
        id="calendarStart"
        :minimum-view="'day'"
        :maximum-view="'day'"
        :value="date"
        :open-date="date"
        :wrapper-class="'start-calendar'"
        :inline="true"
        :disabled-dates="disabledDates"
        @changedMonth="changeAvailabilities"
        @selected="changeDate"
      />
    </div>
  </div>
</template>

<script>
import { inputValidation } from '../../mixins/inputValidation'
import CompleteIcon from './fragments/CompleteIcon'
import InputInfoSection from './fragments/InputInfoSection'
import Datepicker from 'vuejs-datepicker'
import moment from 'moment'
const _ = require('lodash')
const debounceTimout = 300

export default {
  name: 'DateInput',
  components: {
    Datepicker,
    CompleteIcon,
    InputInfoSection
  },
  mixins: [inputValidation],
  data () {
    return {
      localValue: '',
      selectedDate: false,
      date: moment().toDate(),
      calendarLoading: false
    }
  },
  computed: {
    formatedDate () {
      return (this.selectedDate ? moment(this.selectedDate).format('DD MMM YYYY') : '')
    },
    disabledDates () {
      return {
        to: moment().toDate(),
        customPredictor: (date) => {
          let availabilities = this.$store.getters.getAvailabilities
          if (!(moment(date).format('YYYY-MM-DD') in availabilities)) {
            return true
          }
        }
      }
    }
  },
  mounted () {
    if (this.$store.getters.getDate) {
      this.selectedDate = this.$store.getters.getDate
      this.localValue = this.formatedDate
      this.setComplete(true)
    }
  },
  methods: {
    handleFocus () {
      this.isSelecting = true
      this.$nextTick(() => this.$refs.inputElement.focus())
    },
    unfocusInput () {
      this.isSelecting = false
      this.emitInput(this.selectedDate)
    },
    changeDate (selectedDate) {
      this.isSelecting = false
      this.selectedDate = selectedDate
      this.localValue = this.formatedDate
      this.$nextTick(() => this.$refs.inputElement.blur())
    },
    preventKeypress (e) { e.preventDefault() },
    emitInput () {
      if (this.validate()) {
        this.$emit('input', this.selectedDate)
      } else {
        this.$emit('input', false)
      }
    },
    /** API */
    changeAvailabilities (date) {
      this.calendarLoading = true
      this.refetchAvailabilities(date)
    },
    refetchAvailabilities: _.debounce(function(date){
      let id = this.$store.getters.getActivityId
      let today = moment()
      let start = moment(date).startOf('month')
      let end = moment(date).endOf('month')
      if (start.isBefore(today)) { start = today }
      this.$store.dispatch('getAvailabilities', {
        id: id, 
        start: start,
        end: end
      }).then(res => {
        this.$store.dispatch('saveAvailabilities', res.data.data.attributes)
        this.calendarLoading = false
      })
    }, debounceTimout),
  }
}
</script>

<style lang="scss">
.calendar-wrapper{
  display: none;
}
.input-group.is-selecting{
  .calendar-wrapper{
    display: block;
  }
}
</style>
