import axios from 'axios'
const baseUrl = process.env.BASE_ENDPOINT || 'http://www.sb-staging.xyz/api/v1'
const tokenUrl = `${ baseUrl }/auth/anonymous/token`
const experienceEndpoint = `${ baseUrl }/activities`

const apiModule = {
  actions:{
    async getAuthToken () {
      let res = await axios.get(tokenUrl)
      if (res) {
        axios.defaults.headers.common['Authorization'] = `Bearer ${ res.data.token }`
      } else {
        console.log('Error setting token header')
      }
    },
    async getActivity ({ app }, id) { // eslint-disable-line no-unused-vars
      return await axios.get(`${ experienceEndpoint }/${ id }`)
    },
    async getAvailabilities ({ app }, data) { // eslint-disable-line no-unused-vars
      return await axios.get(`${ experienceEndpoint }/${ data.id }/availability?start_date=${ data.start }&end_date=${ data.end }`)
    },
    async getTimeSlots ({ app }, data) { // eslint-disable-line no-unused-vars
      return await axios.get(`${ experienceEndpoint }/${ data.id }/slots?include=prices`)
    },
    async getPrices ({ getters }, data) { // eslint-disable-line no-unused-vars
      let id = getters.getActivityId
      let priceIds = []
      for (let key in data) {
        priceIds.push(data[key].id)
      }
      priceIds = priceIds.join(',')
      return await axios.get(`${ experienceEndpoint }/${ id }/prices?price_ids=${ priceIds }`)
    },
  }
}

export default apiModule
