<template>
  <div :class="{ 'focused': hasData, 'error': displayError }" class="input-group" >
    <label @click="handleInput" v-html="buildLabel" />
    <input
      ref="inputElement"
      :disabled="isDisabled"
      :value="localValue"
      :type="inputType"
      :min="min"
      :max="max"
      @blur="unfocusInput($event)"
      @input="handleInput($event)"
    >
    <CompleteIcon :display="isComplete" />
    <InputInfoSection
      :has-hint="hasHint"
      :hint="hint"
      :display-error="displayError"
      :error-value="errorValue"
    />
  </div>
</template>

<script>
import { inputValidation } from '../../mixins/inputValidation'
import CompleteIcon from './fragments/CompleteIcon'
import InputInfoSection from './fragments/InputInfoSection'

export default{
  name: 'NumberInput',
  components: {
    CompleteIcon,
    InputInfoSection
  },
  mixins: [ inputValidation ],
  props: {
    inputType: {
      type: String,
      default: 'number'
    }
  },
  data () {
    return {
      localValue: null,
    }
  },
  computed: {
    min () { return this.minValue },
    max () { return this.maxValue }
  },
  mounted () {
    switch (this.name){
      case 'adults':
      case 'children':
      case 'spectator':
        let price = this.$store.getters.getPrice(this.inputId)
        if (price.selectedPax > 0) { this.localValue = price.selectedPax }
        break
      case 'shuttle-pax':
        this.localValue = this.$store.getters.getShuttlePax
        break
    }

    this.setComplete(this.hasData)
  },
  methods: {
    handleInput (e) {
      this.localValue = parseInt(e.target.value)
      this.$nextTick(() => this.$refs.inputElement.focus())
      this.clearError()

      if (this.validate()) {
        if (!isNaN(this.localValue)) {
          this.saveValue(this.localValue)
        } else {
          this.saveValue(null)
        }
      } else {
        this.saveValue(null)
      }

      this.emitInput(this.localValue)
    },
    saveValue (value){
      switch (this.name){
        case 'adults':
        case 'children':
        case 'spectator':
          this.$store.commit('updatePax', {
            id: this.inputId, 
            pax: value
          })
          break
        case 'shuttle-pax':
          this.$store.commit('setShuttlePax', value)
          break
      }
    },
    unfocusInput (e) { this.emitInput(parseInt(e.target.value)) },
    emitInput (value) {
      if (this.validate()) {
        this.$emit('input', value)
      } else {
        this.$emit('input', null)
      }
    }
  }
}
</script>
